<script>
import axios from "axios";
import { Button, Notify } from "vant";
export default {
  name: "login",
  data() {
    return {
      username: "",
      password: "",
    };
  },
  components: {
    [Button.name]: Button,
    [Notify.name]: Notify,
  },
  methods: {
    goback(){
      this.$router.go(-1)
    },
    login() {
      axios({
        method: "post",
        url: `https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/login`,
        data: {
          username: this.username,
          password: this.password,
        },
      }).then((res) => {
        if (res.data.msg === "登录成功") {
        // console.log(res);
          let { uid, token, tokenExpired, userInfo } = res.data;
          console.log(uid);
          localStorage.setItem("uid", uid);
          localStorage.setItem("token", token);
          localStorage.setItem("tokenExpired", tokenExpired);
          localStorage.setItem("userInfo", JSON.stringify(userInfo));
          Notify({
            type: "primary",
            message: `登录成功`,
            duration: 1000,
          });
          this.$router.push({
            path: "/index",
          });
          // console.log(1);
        } else {
          Notify({
            type: "warning",
            message: res.data.msg,
            duration: 1000,
          });
          // console.log(res.data.msg);
          // console.log(12);
        }
      });
    },
  },
};
</script>

